import React from "react";
import TopBanner from '../../components/TopBanner'
import Preview from '../../components/Preview'
import Helmet from "react-helmet";

export default function Biceps({ data }) {
  const { edges: posts } = data.allMarkdownRemark;
  return (
    <div className="preview-feed">
      <Helmet title={`Группа мышц - Бицепс`} />
      <TopBanner style={bicepsStyle} title={'Упражнения для бицепса'} />
      {posts
        .filter(post => post.node.frontmatter.filterby === 'biceps')
        .map(({ node: post }) => {
          return (
            <Preview post={post} />
          );
        })}
    </div>
  );
}

const bicepsStyle = {
	backgroundImage: 'url(https://s17.postimg.cc/tfc0ooxwv/biceps.jpg)',
	backgroundPosition: "center"
};

export const pageQuery = graphql`
  query BicepsQuery {
    allMarkdownRemark(sort: { order: DESC, fields: [frontmatter___date] }) {
      edges {
        node {
          excerpt(pruneLength: 180)
          id
          frontmatter {
            title
            date(formatString: "MMMM DD, YYYY")
            path
            image
            image_2
            image_3
            image_4
            filterby
            efficiency
            injury
            bodypart
            filtertype
            type
            filterinventory
            inventory
            filterplace
          }
        }
      }
    }
  }
`;



